.advantages__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-align: center;
  padding: 0 20px;
  margin: 0 0 25px;
}

.advantages__reasons {
  font-size: 14px;
  line-height: 26px;
  text-align: center;
  margin: 0 0 6px;
}

.advantages__list-reasons {
  list-style: none;
  .clearfix();
  background-color: #eeeeee;
}

.advantages__item--wrapper {
  background-color: #81b3d2;
}

.advantages__list-reasons li:last-child {
  padding-bottom: 25px;
}

.advantages__item:nth-child(3),
.advantages__item:nth-child(4) {
  margin-bottom: 25px;
}

.advantages__item-caption {
  display: block;
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  margin: 0 0 10px;
  padding-top: 28px;
}

.advantages__item-caption--line-top {
  width: 280px;
  border-top: 1px solid #ded9d9;
  margin-left: auto;
  margin-right: auto;
}

.advantages__item-caption--style {
  color: @white;
  padding-top: 40px;
}

.advantages__item-caption--triangle {
  background: url("../img/bg-triangle.svg");
  background-repeat: no-repeat;
  background-position: 50% 0;
  padding-top: 78px;
  background-size: contain;
}

.advantages__item-number {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin-bottom: 14px;
}

.advantages__item-number::before,
.advantages__item-number::after {
  content: "";
  width: 10px;
  height: 1px;
  margin: 0 5px;
  background-color: #000000;
}

.advantages__item-number--style {
  color: @white;
}

.advantages__item-number--style::before,
.advantages__item-number--style::after {
  background-color: @white;
}

.advantages__item-text {
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  .clearfix();
  padding: 0 20px;
}

.advantages__item-text--style {
  color: @white;
  padding-bottom: 39px;
}

.advantages__item-text br {
  display: none;
}

.advantages__item:nth-child(4) .advantages__item-text {
  padding: 0 40px;
}

.advantages__item-picture {
  display: block;
  margin: 0 auto;
}

.advantages__list-things {
  padding: 38px 20px 38px;
  margin: 0;
}

@media (min-width: @tablet-width) {
  .advantages__title {
    font-size: 21px;
    padding: 0 160px;
    margin: 0 0 30px;
  }

  .advantages__reasons {
    margin-bottom: 49px;
  }

  .advantages__list-reasons {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  .advantages__item {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }

  .advantages__item img {
    height: 256px;
  }

  .advantages__container {
    display: block;
    width: 255px;
    box-sizing: border-box;
  }

  .advantages__container-reverse {
    order: 2;
  }

  .advantages__item-caption {
    width: 160px;
    margin: 0 auto 10px;
    font-size: 21px;
  }

  .advantages__item-caption--line-top {
    border: none;
  }

  .advantages__item-caption--triangle {
    background: none;
    padding-top: 45px;
  }

  .advantages__item-number {
    margin-bottom: 20px;
  }

  .advantages__item-text {
    width: 180px;
    margin: 0 auto;
  }

  .advantages__item-picture {
    margin: 0;
    padding: 0;
    width: 513px;
  }

  .advantages__item--size {
    width: 33%;
    margin: 0 auto;
    justify-content: center;
  }

  .advantages__container-bottom {
    margin: 10px 0 0 0;
  }

  .advantages__list-things {
    padding: 38px 0 67px;
  }

  .advantages__list-reasons li:last-child {
    padding-bottom: 48px;
  }
}

@media (min-width: @desktop-width) {
  .advantages__title {
    padding: 0 375px;
  }

  .advantages__container {
    width: 400px;
  }

  .advantages__item-caption {
    padding-top: 50px;
  }

  .advantages__item-text {
    width: 290px;
  }

  .advantages__container-bottom {
    margin: -5px 0 0 0;
  }

  .advantages__item-text br {
    display: block;
  }

  .advantages__item-picture {
    width: 800px;
  }

  .advantages__list-reasons li:last-child {
    padding-bottom: 65px;
  }
}
